import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SharedModule } from '../../../shared/modules/shared.module';
import { StoreTransferRoutingModule } from './store-transfer-routing.module';
import { StoreTransferViewComponent } from './store-transfer-view/store-transfer-view.component';
import { StoreTransferComponent } from './store-transfer.component';
import { StoreTransferAddComponent } from './store-transfer-add/store-transfer-add.component';
import { StoreTransferService } from '../../../shared/services/store-transfer.service';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    StoreTransferRoutingModule
  ],
  declarations: [
    StoreTransferComponent,
    StoreTransferAddComponent,
    StoreTransferViewComponent,
  ],
  providers: [
    StoreTransferService
  ],
  entryComponents: [
    StoreTransferAddComponent,
    StoreTransferViewComponent,
  ]
})
export class StoreTransferModule { }



