import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-store-transfer-view',
  templateUrl: './store-transfer-view.component.html',
  styleUrls: ['./store-transfer-view.component.scss']
})
export class StoreTransferViewComponent implements OnInit {

  @Input('storeTransferData') storeTransferData;
  sentItems: any[] = [];
  receivedItems: any[] = [];

  constructor() { }

  ngOnInit() {
    this.storeTransferData.store_transfer_items.forEach(result => {
      if(result.type == 1) {
        this.receivedItems.push(result);
      } else {
        this.sentItems.push(result);
      }
    });
    console.log(this.sentItems);
    console.log(this.receivedItems);
  }

}



