import { Component, OnInit, ViewChild, Output, EventEmitter, Inject } from '@angular/core';
import { jqxDateTimeInputComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxdatetimeinput';
import { FormBuilder, FormGroup, Validators, FormArray } from '@angular/forms';
import { ItemMasterService, CurrentUserService, ErrorHandleService } from '../../../../shared';
import { StoreTransferService } from '../../../../shared/services/store-transfer.service';
import { jqxLoaderComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxloader';
import { jqxNotificationComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxnotification';
import { jqxComboBoxComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxcombobox';

@Component({
  selector: 'app-store-transfer-add',
  templateUrl: './store-transfer-add.component.html',
  styleUrls: ['./store-transfer-add.component.scss']
})
export class StoreTransferAddComponent implements OnInit {

  @ViewChild('dateInput') dateInput: jqxDateTimeInputComponent;
  @Output() closeWindow = new EventEmitter();
  @Output() updateGrid = new EventEmitter();
  @Output() AddedData = new EventEmitter();
  @ViewChild('jqxLoader') jqxLoader: jqxLoaderComponent;
  @ViewChild('winNotification') winNotification: jqxNotificationComponent;
  @ViewChild("sComboBox") sComboBox: jqxComboBoxComponent;
  @ViewChild("sComboBox1") sComboBox1: jqxComboBoxComponent;
  storeTransferForm: FormGroup;
  sentItemFocus: any = [];
  sentItemAdapter: any = [];
  ItemFocus: boolean = false;
  ItemAdapter: any;
  storeAdapter: any;
  store_data: any;
  private fromStoreId: number = 0;
  private toStoreId: number = 0;
  private apiUrl: string;
  fromStore: boolean = false;
  toStore: boolean = false;
  userData: any = {};

  constructor(
    private fb: FormBuilder,
    private ims: ItemMasterService,
    private sts: StoreTransferService,
    private cus: CurrentUserService,
    private ehs: ErrorHandleService,
    @Inject('API_URL') apiUrl: string,
  ) {
    this.apiUrl = apiUrl;
    this.userData['token'] = this.cus.getTokenData()['token'];
  }

  ngOnInit() {
    this.storeTransferForm = this.fb.group({
      date: '',
      from_store_id: null,
      to_store_id: null,
      storetransfers: this.fb.array([
      ]),
      sent_items: this.fb.array([
        this.initSendItems(),
      ]),
      received_items: this.fb.array([
        this.initReceivedItems()
      ])
    })

    this.store_data =
      {
        datatype: "json",
        contentType: "application/json; charset=utf-8",
        datafields: [
          { name: 'id' },
          { name: 'name' },
        ],
        data: this.userData,
        url: this.apiUrl + 'store?limit=all',
      };
    this.storeAdapter = new jqx.dataAdapter(this.store_data, {
      downloadComplete: (data, status, xhr) => {
        let tData = data.data;
        return tData;
      }
    });
  }

  initSendItems() {
    return this.fb.group({
      item_id: [null],
      item_name: [null],
      type: '0',
      quantity: [null, Validators.required],
      rate: [null, Validators.required],
      value: [null, Validators.required]
    });
  }

  initReceivedItems() {
    return this.fb.group({
      item_id: [null],
      item_name: [null],
      type: '1',
      quantity: [null, Validators.required],
      rate: [null, Validators.required],
      value: [null, Validators.required]
    });
  }

  addItem(items) {

    if (items == 'sent_items') {
      const control1 = <FormArray>this.storeTransferForm.controls['sent_items'];
      control1.push(this.initSendItems());

      const control2 = <FormArray>this.storeTransferForm.controls['received_items'];
      control2.push(this.initReceivedItems());
    } else {
      const control2 = <FormArray>this.storeTransferForm.controls['received_items'];
      control2.push(this.initReceivedItems());

      const control1 = <FormArray>this.storeTransferForm.controls['sent_items'];
      control1.push(this.initSendItems());
    }
  }

  removeItem(i: number, items) {
    if (items == 'sent_items') {
      const control1 = <FormArray>this.storeTransferForm.controls['sent_items'];
      control1.removeAt(i);

      const control2 = <FormArray>this.storeTransferForm.controls['received_items'];
      control2.removeAt(i);
    } else {
      const control2 = <FormArray>this.storeTransferForm.controls['received_items'];
      control2.removeAt(i);

      const control1 = <FormArray>this.storeTransferForm.controls['sent_items'];
      control1.removeAt(i);
    }
  }

  sentItemListSelected(selectedEvent, index, items) {
    if (selectedEvent && selectedEvent.target && selectedEvent.target.value) {
      if (items == 'received_items') {
        let displayText = selectedEvent.target.selectedOptions[0].text;
        this.storeTransferForm.get('received_items')['controls'][index].get('item_id').patchValue(selectedEvent.target.value);
        this.storeTransferForm.get('received_items')['controls'][index].get('item_name').patchValue(displayText);

        this.storeTransferForm.get('sent_items')['controls'][index].get('item_name').patchValue(displayText);
        this.storeTransferForm.get('sent_items')['controls'][index].get('item_id').patchValue(selectedEvent.target.value);
        this.sentItemFocus[index] = false;
      } else {
        let displayText = selectedEvent.target.selectedOptions[0].text;
        this.storeTransferForm.get('sent_items')['controls'][index].get('item_id').patchValue(selectedEvent.target.value);
        this.storeTransferForm.get('sent_items')['controls'][index].get('item_name').patchValue(displayText);

        this.storeTransferForm.get('received_items')['controls'][index].get('item_name').patchValue(displayText);
        this.storeTransferForm.get('received_items')['controls'][index].get('item_id').patchValue(selectedEvent.target.value);
        this.sentItemFocus[index] = false;
      }
    }
  }

  sentItemFilter(searchPr, index) {
    let keycode = searchPr['keyCode'];
    if ((keycode == 40)) {
      document.getElementById('itemCode2').focus();
    }
    let searchString = searchPr['target'].value;
    let len = searchString.length;
    let dataString = searchString.substr(len - 1, len);
    let temp = searchString.replace(' ', '');
    temp = temp.replace(/\s/g, '');
    if (dataString == ' ' && temp.length > 1) {
      if (temp) {
        this.sentItemFocus[index] = true;
        this.ims.index({ code: temp, transact: 1, limit: 'all' }).subscribe(
          response => {
            this.sentItemAdapter[index] = [];
            this.sentItemAdapter[index] = response;
          },
          error => {
            console.log(error);
          }
        );

      } else {
        this.sentItemFocus[index] = false;
      }
    }
  }

  save(post) {
    this.jqxLoader.open();
    post.date = this.dateInput.val();
    post.from_store_id = this.fromStoreId;
    post.to_store_id = this.toStoreId;

    if (post.sent_items.length == post.received_items.length) {
      for (let i = 0; i < post.sent_items.length; i++) {
        post.storetransfers.push(post.sent_items[i]);
        post.storetransfers.push(post.received_items[i]);
      }
    }

    delete post['received_items'];
    delete post['sent_items'];

    this.sts.store(post).subscribe(
      (result: any) => {
        let data = this.ehs.formatError(result);
        if (data['result'] == true) {
          this.AddedData.emit(data);
          this.updateGrid.emit();
        } else {
          let messageDiv: any = document.getElementById('windowMessage');
          messageDiv.innerHTML = '';
          for (let i = 0; i < data['message'].length; i++) {
            let errorDiv = document.createElement('li');
            errorDiv.innerText = data['message'][i];
            messageDiv.appendChild(errorDiv);
          }
          this.winNotification.open();
        }
        this.jqxLoader.close();
      }, error => {
        let data = this.ehs.formatError(error);
        let messageDiv: any = document.getElementById('windowMessage');
        messageDiv.innerText = data['message'];
        this.winNotification.open();
        this.jqxLoader.close();
      }
    )

  }

  cancelBtn() {
    this.closeWindow.emit();
  }

  getFromStoreId($event) {
    this.fromStoreId = $event.args.item.originalItem.id;
    this.fromStore = this.sComboBox.val() ? true : false;
  }

  getToStoreId($event) {
    this.toStoreId = $event.args.item.originalItem.id;
    this.toStore = this.sComboBox1.val() ? true : false;
  }

  public valueChanged($event, i, value) {
    if (value == 'sent') {
      let rate = this.storeTransferForm.get('sent_items')['controls'][i].get('rate').value;
      let quantity = this.storeTransferForm.get('sent_items')['controls'][i].get('quantity').value;

      if (rate && quantity) {
        let total = parseFloat(rate) * parseFloat(quantity);
        this.storeTransferForm.get('sent_items')['controls'][i].get('value').patchValue(total);

        this.storeTransferForm.get('received_items')['controls'][i].get('rate').patchValue(rate);
        this.storeTransferForm.get('received_items')['controls'][i].get('quantity').patchValue(quantity);
        this.storeTransferForm.get('received_items')['controls'][i].get('value').patchValue(total);
      }

    } else {
      let rate = this.storeTransferForm.get('received_items')['controls'][i].get('rate').value;
      let quantity = this.storeTransferForm.get('received_items')['controls'][i].get('quantity').value;

      if (rate && quantity) {
        let total = parseFloat(rate) * parseFloat(quantity);
        this.storeTransferForm.get('received_items')['controls'][i].get('value').patchValue(total);
      }
    }

  }


}



